.partners-slider {
  margin-bottom: 40px; }
  .partners-slider #partners-slider {
    padding: 0 20px; }
  .partners-slider_container {
    display: block;
    padding-left: 10px;
    padding-right: 10px;
    border: 3px solid green; }
    @media screen and (min-width: 980px) {
      .partners-slider_container {
        max-width: 980px;
        margin: 0 auto;
        padding-left: 20px;
        padding-right: 20px; } }
  .partners-slider_wrap {
    position: relative;
    padding-top: 10px;
    border: 1px solid red; }
  .partners-slider_item {
    height: 65px;
    line-height: 65px;
    vertical-align: middle;
    text-align: center; }
    .partners-slider_item img {
      display: inline-block !important;
      vertical-align: middle;
      margin: 0 auto; }
  .partners-slider_prev, .partners-slider_next {
    display: block;
    position: absolute;
    border: 0;
    top: 50%;
    transform: translateY(-50%);
    background: url("../img/partners-slider-arrow.png") no-repeat;
    background-size: 23px 42px;
    width: 23px;
    height: 42px;
    z-index: 1000;
    opacity: .4;
    transition: .15s;
    cursor: pointer; }
    .partners-slider_prev:hover, .partners-slider_next:hover {
      opacity: .9; }
  .partners-slider_next {
    right: 0; }
  .partners-slider_prev {
    left: 0;
    transform: translateY(-50%) rotate(180deg); }
